/* Dual Sliding Panel Media Queries */
.panel-container {
	color: black;
	overflow: hidden;
}
.panels__headline {
	margin: 0;
	text-align: center;
	color: #1a1a1a;
}
.panels p {
	margin: 0;
	font-family: "Roboto", sans-serif !important;
	text-align: center;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 640px) {
	/* Styles */
	.panels__headline {
		font-size: 28px;
	}

	.panels p {
		font-size: 16px;
	}
	.panel-container {
		height: 100vh;
	}
}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width: 321px) {
