	/* Styles */
}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width: 640px) and (max-device-width: 1224px) {
	/* Styles */
	.panels__headline {
		font-size: 24px;
	}

	.panels p {
		font-size: 14px;
	}
	.panel-container {
		height: 60vh;
	}
	.panels__side {
		height: 60vh;
	}
}

/* iPads (landscape) ----------- */
