/* Desktops and laptops ----------- */
@media only screen and (min-width: 1224px) {
	/* Styles */
	.panels__headline {
		font-size: 30px;
	}

	.panels p {
		font-size: 22px;
	}
	.panel-container {
		height: 60vh;
	}
	.panels__side {
		height: 60vh;
	}
}

/* Large screens ----------- */
@media only screen and (min-width: 1824px) {
	/* Styles */
	.panels__headline {
		font-size: 50px;
	}

	.panels p {
		font-size: 28px;
	}

	.panel-container {
		height: 80vh;
	}
	.panels__side {
		height: 80vh;
	}
}

